<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-spacer></v-spacer>
        
        <v-card class="ma-1" cols="12">
        <v-container>
            <v-row>
            <v-col cols="12">
              <v-autocomplete
                outlined
                dense
                placeholder="STORES"
                class="mx-4 mb-n6"
                v-model="selected"
                chips
                :items="items"
                search-input="items"
                multiple
              >
                <template v-slot:prepend-item>
                  <v-list-item
                    ripple
                    @click="toggle"
                  >
                    <v-list-item-action>
                      <v-icon :color="selected.length > 0 ? '#E49711' : ''">
                        {{ icon }}
                      </v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title>
                        Select All
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider class="mt-2"></v-divider>
                </template>
              </v-autocomplete>
            </v-col>
            </v-row>

            <v-row>
                <v-col cols="12">
                    <h5 class="mx-4">*Amount:</h5>
                <v-text-field
                class="mx-4 mb-n6"
                outlined
                dense
                prepend-inner-icon="mdi-currency-usd"
                v-model="extra.amount"
                ></v-text-field>
                </v-col>
                <v-col cols="12">
                    <h5 class="mx-4">*Due date:</h5>
                <v-text-field
                class="mx-4 mb-n6"
                label="aaaa-mm-dd"
                outlined
                dense
                prepend-inner-icon="mdi-calendar"
                v-model="extra.due_date"
                ></v-text-field>
                </v-col>
                <v-col cols="12">
                    <h5 class="mx-4">*Description:</h5>
                <v-text-field
                class="mx-4 mb-n6"
                outlined
                dense
                v-model="extra.description"
                ></v-text-field>
                </v-col>
            </v-row>
            <v-col>
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            @click="$router.go(-1)"
            >
            GO BACK
            </v-btn>
            <v-btn
            class="mx-4 my-2 primary"
            light
            :loading="loading"
            @click="add"
            >
            ADD INVOICE
            </v-btn>
            </v-col>
        </v-container>
        </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import StoreService from '@/services/StoreService';
import InvoiceService from '@/services/InvoiceService';
import Swal from 'sweetalert2'

export default {
    components: {
      NavBar
    },

    data: () => ({
        title: 'ADD EXTRA INVOICE',
        loading: false,
        items: [

        ],
        selected: [],
        extra:{
            amount: '',
            due_date: '',
            description: '',
            id_stores: [],
        }
    }),
    
    computed: {
      likesAll () {
        return this.selected.length === this.items.length
      },
      likesSome () {
        return this.selected.length > 0 && !this.likesAll
      },
      icon () {
        if (this.likesAll) return 'mdi-close-box'
        if (this.likesSome) return 'mdi-minus-box'
        return 'mdi-checkbox-blank-outline'
      },
    },

    mounted() {
      StoreService
      .list_admin()
      .then(res => {
        for (let i = 0; i < res.length; i++) {
              let store = res[i];
              this.items.push({ text: store.name, value: store.unique_id })
          }
      })
      .catch(e => {
          /* 
          *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
          */
      let error = e.response.data;
      let msg = ''
      Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
      });
          this.loading = false;
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          });
      })

    },

    methods: {
      toggle () {
        this.$nextTick(() => {
          if (this.likesAll) {
            this.selected = []
          } else {
            this.selected = this.items.slice()
          }
        })
      },

      add(){
          this.loading = true;
          this.extra.id_stores = this.selected;
          InvoiceService
          .add_extra(this.extra)
          .then(res => {
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'Invoices succesfully created.'
            }).then(() => {
                this.loading = false
                this.$router.go(-1)
              })
          })
          .catch(e => {
              /* 
              *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
              */
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
          });
              this.loading = false;
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
      }
    }

}
</script>